.parent:after{
    content: "";
    display:table;
    clear: both;
}

.descriptionText{
    position:absolute;
    color: white;
    top:0%;
    padding: 10px;
    /* transform: translate(-50%, -50%); */
    /* -ms-transform: translate(-50%, -50%); */
}

.bing-photo{
    float:left;
}

.imgdesc-con{
    float:right;
    position:absolute;
    bottom:0;
    left: 0;
    right: 0;
    transition:.5s ease;
    background-color: rgba(0, 0, 0, 0.8);
    overflow: hidden;
    width: 90%;
    height: 0;
}

.pic-col:hover .imgdesc-con{
    height:100%;
}