.imgdesc{
    position:absolute;
    bottom:0;
    left: 0;
    right: 0;
    transition:.5s ease;
    background-color: rgba(0, 0, 0, 0.8);
    overflow: hidden;
    width: 100%;
    height: 0;
    
}

.descriptionText{
    position:absolute;
    color: white;
    top:0%;
    padding: 10px;
    /* transform: translate(-50%, -50%); */
    /* -ms-transform: translate(-50%, -50%); */
}

.homeText{
   font-size: large;
   font-family: Georgia, 'Times New Roman', Times, serif;
}

