.Collapsible{

margin-top: 0%;
margin-bottom: 10pt;
}
.Collapsible:hover{
    background-color: rgba(154, 155, 165, 0.358);
}
.Collapsible__trigger{
    display: flex;
    justify-content: space-between;
    color: black;
    padding-left: .1rem;
   font-size: 20pt;
}
.Collapsible__trigger svg{
    color:white;
    align-items: center;
    margin-right: .5rem;
}

.Collapsible__trigger.is-open svg {
    transform: rotate(180deg);
  }
.Collapsible__contentInner{
    color:black;
}

.Collapsible__contentOuter{
    overflow:hidden;
}


